table {
  width: 100%;
}

.example-container tr td:last-child,
.example-container tr th:last-child {
  max-width: 150px;
  width: 100px;
  white-space: nowrap;
  text-align: left;
}

th.mat-header-cell {
  background: #0072ce;
}

table tr th:first-child {
  border-radius: 5px 0 0 0px !important;
}

table tr th:last-child {
  border-radius: 0px 5px 0 0px !important;
}

td.mat-cell:last-of-type,
td.mat-footer-cell:last-of-type,
th.mat-header-cell:last-of-type {
  text-align: left;
}

.col {
  min-width: 0;
  max-width: 100%;
  padding: 0;
  font-size: 13px !important;
}

.colLink {
  min-width: 0;
  max-width: 100%;
  padding: 0;
  font-size: 13px !important;
  color: #1072ce;
  cursor: pointer;
}

.test-note {
  cursor: pointer;
}

.col-sm-4 {
  flex: 0 0 33.333333%;
  max-width: 31.333333%;
  padding-right: 0;
}

.range .mat-form-field-flex {
  padding-left: 2px;
  padding-right: 1px;
}

.range input.mat-input-element {
  font-size: 13px;
}

.lstrow {
  background: #ed8b00;
  color: #fff;
  padding: 12px 10px;
  border-radius: 0px 0px 5px 5px;
}

.lstrow span {
  width: 100px;
  text-align: center;
}

.resultdata {
  background: #f5fbf7;
  padding: 4px;
  text-align: center;
  border-radius: 7px;
  width: 42px;
  display: block;
  font-size: 12px;
}
  
/* Toggle  */

.c-point {
  cursor: pointer;
}

.prebtn {
  position: absolute;
  background: #fff;
  border: none;
  top: 4px;
  left: -4px;
}

.nxtbtn {
  position: absolute;
  background: #fff;
  border: none;
  top: 4px;
  right: 0px;
}

.pink {
  background: #ffeee3;
  display: flex;
  height: 48px;
  width: 150px;
  align-items: center;
  line-height: 1.2;
  padding-left: 5px;
}

td.mat-cell:first-of-type,
td.mat-footer-cell:first-of-type,
th.mat-header-cell:first-of-type {
  padding-left: 42px !important;
}

.left_arrow {
  position: absolute;
  left: 7px;
  top: 7px;
  z-index: 999;
}

.right_arrow {
  position: absolute;
  right: 9px;
  top: 7px;
  z-index: 999;
}

#result-block {
  min-width: 170px;
  max-width: 170px;
  width: 170px;
}

.date_block {
  max-width: 95px;
  white-space: normal;
  display: block;
}

.sticky-top {
  position: sticky;
  top: 6px;
}

.tablecont {
  align-items: center;
  justify-content: center;
  line-height: 1.2;
  height: 48px;
  width: 150px;
}

td.mat-cell.datas {
  max-width: 260px;
  width: 160px;
  white-space: nowrap;
}

td.mat-cell:nth-child(2) {
  width: 100px;
}

td.mat-cell.nodata {
  width: 1px;
}

td.datepart {
  width: 150px;
}

td.mat-cell,
td.mat-footer-cell,
th.mat-header-cell {
  padding: 0 10px;
}